<template>
	<div class="weex">
		<topic-list :topics="topics"></topic-list>
		<infinite-loading @infinite="infiniteHandler" v-show="loadingIsShow"></infinite-loading>
	</div>
</template>

<script>
	// ÂºïÂÖ•ËØ∑Ê±ÇÊï∞ÊçÆÁöÑÊ®°Âùó
	import { getTopicList } from "@/getdata/index.js";
	// ÂºïÂÖ•‰∏ªÈ¢ò ÂàóË°® ÁªÑ‰ª∂
	import TopicList from "@/components/topics/TopicList";
	// Âä†ËΩΩ‰∏ä‰∏ãÊªëÂä®Ê®°Âùó
	import InfiniteLoading from 'vue-infinite-loading';

	export default {
		name: 'Weex',
		data() {
			return {
				topics: [],
				page: 1,
				loadingIsShow: true
			}
		},
		// Âä†ËΩΩÁªÑ‰ª∂
		components: {
			TopicList
		},
		// Èí©Â≠êÂáΩÊï∞
		mounted() {
			// Ë∞ÉÁî®Êü•ËØ¢Êï∞ÊçÆÁöÑÂáΩÊï∞
			getTopicList({tab: 'weex',limit: 20}).then((data) => {
				this.topics = data.data.data
			})
		},
		methods: {
			infiniteHandler($state) {
				setTimeout(() => {
					getTopicList({limit: 10,tab: 'weex',page: ++(this.page)}).then(data => {
						this.topics = this.topics.concat(data.data.data);
						if(data.data.data.length == 0) {
							this.loadingIsShow = false;
						} else {
							$state.loaded();
						}
					})
				}, 1000);
			}
		},
	}
</script>

<style lang="scss">
	@import "../../assets/sass/base.scss";
	.weex {
		margin-bottom: rem(200px);
	}
</style>


