<template>
	<div class="all">
		<!-- Â≠êÁªÑ‰ª∂ -->
		<topic-list :topics="topics"></topic-list>
		<!--Âä†ËΩΩÁªÑ‰ª∂-->
		<infinite-loading @infinite="infiniteHandler"></infinite-loading>
	</div>
</template>

<script>
	// ÂºïÂÖ•ËØ∑Ê±ÇÊï∞ÊçÆÁöÑÊ®°Âùó
	import { getTopicList } from "@/getdata/index.js";
	// ÂºïÂÖ•‰∏ªÈ¢ò ÂàóË°® ÁªÑ‰ª∂
	import TopicList from "@/components/topics/TopicList";
	// Âä†ËΩΩ ‰∏ä‰∏ãÊªëÂä®Ê®°Âùó
	import InfiniteLoading from 'vue-infinite-loading';
	
	export default {
		name: 'All',
		// Êï∞ÊçÆ
		data() {
			return {
				topics: [],
				//È°µÈù¢
      			page:1,
      			//ÊéßÂà∂È°µÈù¢ÊªëÂä®Âà∞Â∫ïÈÉ®,Â¶ÇÊûúÊúâÊï∞ÊçÆÂ∞±ÊòæÁ§∫loading,Â¶ÇÊûúÊ≤°ÊúâÂ∞±‰∏çÊòæÁ§∫
      			loadingIsShow:true
			}
		},
		// Âä†ËΩΩÁªÑ‰ª∂
		components: {
			TopicList,
			InfiniteLoading
		},
		 // Èí©Â≠êÂáΩÊï∞
		mounted(){
			// Ë∞ÉÁî®Êü•ËØ¢Êï∞ÊçÆÁöÑÂáΩÊï∞
		    getTopicList({limit:20,page:this.page}).then((data)=>{
		    	this.topics = data.data.data
		    })
		},
		methods:{
		    infiniteHandler($state) {
		    	setTimeout(() => {
		        	getTopicList({limit:10,page:++(this.page)}).then(data=>{
		        	// console.log(data);
		        	this.topics = this.topics.concat(data.data.data);
		        	// ÂèñÊï∞ÊçÆÁöÑÊó∂ÂÄôÂà§Êñ≠data.data.dataÁöÑlength  
			        if(data.data.data.length==0){
			            this.loadingIsShow = false;
			        }else{
			            // ÈáçÊñ∞ËØ∑Ê±ÇÊï∞ÊçÆ
			            $state.loaded();  
			        }
		        })
		        // ÈáçÊñ∞ËØ∑Ê±ÇÊï∞ÊçÆ
		        $state.loaded();  
		      }, 1000);
		    }
		},
	}
</script>
<style lang="scss">
/*Âä†ËΩΩÂü∫Á°ÄÁöÑÊ†∑Âºè*/
@import "../../assets/sass/base.scss";
.all{
  margin-bottom: rem(100px);
}
</style>


