	//Áî®Êù•ËØ∑Ê±ÇÊï∞ÊçÆÁöÑÊ®°Âùó
	//Âä†ËΩΩaxiosÊ®°Âùó(Áî®Êù•ÂèëÈÄÅajaxÁöÑËØ∑Ê±ÇÁöÑ)
	import axios from 'axios';
	
	// Âü∫Êú¨ÁöÑÈÖçÁΩÆ
	var $http = axios.create({
	    baseURL: 'https://www.vue-js.com/api/v1/',
	    headers: { 'X-Custom-Header': 'foobar' }
	});
	

	//ËØ∑Ê±Ç‰∏ªÈ¢òÂàóË°®ÁöÑÂáΩÊï∞
	function getTopicList(params){
		
		//ÈªòËÆ§ÁöÑÂèÇÊï∞
	    var newParams = Object.assign({
	        limit:20
	    }, params)
	
	    //ËøîÂõûÂÄº
	    return $http({
	        //ËØ∑Ê±ÇÁöÑÊñπÊ≥ï
	        method: 'get', 
	        //ËØ∑Ê±ÇÁöÑurl‰ºöÊãºÊé•Âà∞baseURL‰∏≠
	        url: '/topics',
	        params: newParams
	    });
	}
	
	// Êü•‰∏ªÈ¢ò ÂÜÖÂÆπ ÁöÑÂáΩÊï∞
	function getTopicShow(id){
	    return $http({
	        method: 'get',
	        // ËØ∑Ê±ÇÁöÑurl ‰ºöÊãºÊé• Âà∞ baseURL ‰∏≠
	        url: '/topic/'+id,   
	    })
	}
	
	export { getTopicList, getTopicShow}
	



